import { client } from '.'
import { APIResponse } from '../redux/types/APITypes'

interface ResData {
	token: string
}

class AuthService {
	static async login(username: string, password: string) {
		const { data } = await client.put<APIResponse<ResData>>('/auth/login', { username, password })
		return data
	}

	static async register(username: string, password: string, confirmPassword: string) {
		const { data } = await client.post<APIResponse<ResData>>('/auth/register', { username, password, confirmPassword })
		return data
	}
}

export default AuthService
